<template>
  <Title/>
  <Timer :key="componentKey" :stopFn="stop"/>
  <Footer/>
</template>

<script>
import Timer from './components/timer.vue'
import Title from './components/title.vue'
import Footer from './components/footer.vue'

export default {
  name: 'App',
  components: { Timer, Title, Footer },
  data() {
    return {
      componentKey: 0
    }
  },
  methods: {
    stop() {
       this.componentKey += 1
    }
  }
}
</script>

