<template>
    <Title>
        <h1 v-if="focus === 'focus'">Focus Time!</h1>
        <h1 v-else-if="focus === 'rest'">Rest Time!</h1>
        <h1 v-else>Pomodoro Timer</h1>
    </Title>
    <div>
        <div v-if="minutes > 9 && seconds > 9" class="timer">{{ minutes }}:{{ seconds }}</div>
        <div v-else-if="minutes == 0 && seconds == 0" class="timer">25:00</div>
        <div v-else-if="minutes < 10 && seconds < 10" class="timer">0{{ minutes }}:0{{ seconds }}</div>
        <div v-else-if="seconds < 10" class="timer">{{ minutes }}:0{{ seconds }}</div>
        <div v-else-if="minutes < 10" class="timer">0{{ minutes }}:{{ seconds }}</div>
        <div class="btn-container">
            <button type="button" @click="focusCount" class="btn">Start</button>            
            <button type="button" @click="stopFn()" class="btn">Stop</button>            
        </div>
    </div>  
</template>

<script>
import Title from './title.vue'
export default {
    components: { Title },
    props: {
        stopFn: Function
    },
    data() {
        return {
            value: 1,
            focusTime: 60000 * 25,
            restTime: 60000 * 5,
            minutes: 0,
            seconds: 0,
            intervalDuration: 0,
            interval: 0,
            focus: '',
            bellSound: new Audio(require('@/assets/bell.mp3'))
        }
    },
    methods: {        
        focusCount() {
            this.focus = 'focus'
            this.intervalDuration = this.focusTime
            this.interval = setInterval(() => {
                if (this.intervalDuration <= 0) {
                    this.bellSound.play()
                    clearInterval(this.interval)
                    this.restCount()
                }
                this.minutes = Math.floor((this.intervalDuration % (1000 * 60 * 60)) / (1000 * 60))
                this.seconds = Math.floor((this.intervalDuration % (1000 * 60)) / 1000)
                this.intervalDuration -= 1000
                }, 1000)
            },
            restCount() {
                this.focus = 'rest'
                this.intervalDuration = this.restTime
                this.interval = setInterval(() => {
                    if (this.intervalDuration <= 0) {                                                          
                        this.bellSound.play()
                        clearInterval(this.interval)
                        this.focusCount()                                
                    }
                    this.minutes = Math.floor((this.intervalDuration % (1000 * 60 * 60)) / (1000 * 60))
                    this.seconds = Math.floor((this.intervalDuration % (1000 * 60)) / 1000)
                    this.intervalDuration -= 1000
                    }, 1000)
            }
    }
};

</script>

<style>
.timer {
  font-size: 6rem;
  align-self: center;
  margin-bottom: 10px;
}

.btn {
  align-self: center;
  color: rgb(255, 255, 255);
  font-size: 1.3rem;
  width: 70px;
  height: auto;
  background-color: rgb(5, 133, 5);
  border-style: none;
  border-radius: 10px;
  margin: 5px 5px;
}

.btn:hover {
  background-color: rgb(4, 112, 4);
}

.btn:active {
  transform: translateY(4px);
}

.btn:focus {
  outline: none;
}

</style>